import React from "react"
import { graphql, useStaticQuery } from "gatsby"
import Layout from "../components/layout"
import Seo from "../components/seo"
import ContactExpert from "../components/contactExpert"

import HomeSlider from "../components/homeSlider"
import HomeAbout from "../components/homeAbout"
import HomeProperty from "../components/homeProperty"
import HomeJourney from "../components/homeJourney"
import HomeProcess from "../components/homeProcess"
import HomePropertyType from "../components/homePropertyType"
import HomeCounter from "../components/homeCounter"
import HomeServices from "../components/homeServices"
import HomeTestimony from "../components/homeTestimony"
import HomeGuide from "../components/homeGuide"
import HomeBlog from "../components/homeBlog"

const IndexPage = () => {
  const data = useStaticQuery(graphql`
    query MyQuery {
      allWpPage {
        nodes {
          homePage {
            banner {
              bannerImage {
                node {
                  sourceUrl
                  altText
                }
              }
              heading
              hmBannerContent
            }
            about {
              heading
              content
              count
              caption
            }
            journey {
              heading
              hmJourneyCta {
                ctaLabel
                ctaLink
              }
              journeySteps {
                content
                image {
                  node {
                    sourceUrl
                    altText
                  }
                }
              }
            }
            processes {
              heading
              process {
                process
              }
            }
            services {
              servicesContent
              services {
                serviceIcon {
                  node {
                    sourceUrl
                    altText
                  }
                }
                service
              }
              otherServices
            }
            counter {
              heading
              counters {
                count
                additionalSymbol
                content
              }
            }
            guide {
              guideSlides {
                guideContent
              }
            }
          }
        }
      }
      allWpTestimony {
        nodes {
          title
          content
        }
      }
      wp {
        themeGeneralSettings {
          contactExpert {
            contactExpert
          }
        }
      }
      allWpPost {
        nodes {
          title
          featuredImage {
            node {
              sourceUrl
              altText
            }
          }
        }
      }
      allWpProperty {
        nodes {
          title
          slug
          featuredImage {
            node {
              sourceUrl
              altText
            }
          }
          propertySection {
            location
            noOfRooms
            noOfBathrooms
            totalArea
            price
          }
        }
      }
    }
  `)

  // unknown solution
  let index = 0
  while (true) {
    if (data.allWpPage.nodes[index].homePage.banner.bannerImage) {
      break
    }
    index++
  }

  const banner = data.allWpPage.nodes[index].homePage.banner
  const { bannerImage, heading, hmBannerContent } = banner

  const about = data.allWpPage.nodes[index].homePage.about
  const { heading: aboutHeading, content, count, caption } = about

  const journey = data.allWpPage.nodes[index].homePage.journey
  const { heading: journeyHead, journeySteps } = journey

  const process = data.allWpPage.nodes[index].homePage.processes
  const { heading: ProcessHead, process: ProcessStep } = process

  const guide = data.allWpPage.nodes[index].homePage.guide.guideSlides

  const services = data.allWpPage.nodes[index].homePage.services
  const counter = data.allWpPage.nodes[index].homePage.counter

  const expertContent =
    data.wp?.themeGeneralSettings?.contactExpert?.contactExpert || ""
  const testimonials = data.allWpTestimony?.nodes || ""

  const blogs = data.allWpPost.nodes

  const properties = data.allWpProperty.nodes

  return (
    <Layout>
      <HomeSlider
        bannerImage={bannerImage ? bannerImage.node.sourceUrl : ""}
        altText={bannerImage ? bannerImage.node.altText || "Propey" : ""}
        heading={heading}
        hmBannerContent={hmBannerContent}
      />

      {aboutHeading && content && (
        <HomeAbout
          count={count}
          caption={caption}
          aboutHeading={aboutHeading}
          content={content}
        />
      )}

      {properties && (
        <HomeProperty
          heading="Properties"
          content="Here are some options for you to choose from"
          properties={properties}
          cta="/"
        />
      )}

      {journeySteps && (
        <HomeJourney
          journeyHead={journeyHead}
          journeySteps={journeySteps}
          ctaLink={journey.hmJourneyCta.ctaLink}
          ctaLabel={journey.hmJourneyCta.ctaLabel}
        />
      )}

      {ProcessStep && (
        <HomeProcess ProcessHead={ProcessHead} ProcessStep={ProcessStep} />
      )}

      <HomePropertyType />

      {services && <HomeServices services={services} />}

      {counter && <HomeCounter counter={counter} />}

      {testimonials && <HomeTestimony testimonials={testimonials} />}

      {guide && <HomeGuide guide={guide} />}

      {blogs && <HomeBlog blogs={blogs} />}

      <ContactExpert html={expertContent} />
    </Layout>
  )
}

export const Head = () => <Seo title="Home" />

export default IndexPage
